.bookSlider {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 150px;
  width: 5.5rem;
  overflow: hidden;
  padding: 5px 0px;
  margin-left: 10px;
  /* border: 1px solid black; */
  border-radius: 10px;
  /* background-color: rgba(128, 49, 202, 0.6); */
}

.bookSlider_img {
  width: 90%;
  height: 80%;
  /* border-radius: 10px; */
  /* border: 1px solid black; */
  object-fit: contain;
  transition: opacity 0.3s ease-in-out; /* Плавный переход для opacity */
  opacity: 1; /* Исходная видимость */
}

.bookSlider_img.fade {
  opacity: 0; /* Скрыть изображение при переходе */
}

.bookSlider_title {
  font-size: 12px;
}
